.animate-route {
    animation-duration: 1s;
    animation-fill-mode: both;

    animation: fadeRoute .2s linear;
}

@keyframes fadeRoute {
    from {
        left: -100%;
        opacity: 0;
    }

    to {
        left: 0;
        opacity: 1;
    }
}

@keyframes loginBtnBg {
    0% {
        opacity: .2;
    }

    33% {
        opacity: .8;
    }

    66% {
        opacity: 1;
    }

    100% {
        opacity: 0.2;
    }
}